import * as LIST_CONS from '../constants/user';
import Axios from 'axios';

export const signin = (email, password) => async (dispatch) => {
    dispatch({
        type: LIST_CONS.USER_SIGNIN_REQUEST,
        payload: { email, password }
    })
    try {
        const { data } = await Axios.post('/api/users/signin', { email, password });
        dispatch({
            type: LIST_CONS.USER_SIGNIN_SUCCESS,
            payload: data,
        })
        localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
        dispatch({
            type: LIST_CONS.USER_SIGNIN_FAIL,
            payload: error.response && error.response.data.message ? error.response.data.message : error.message
        })
    }
}

export const signout = () => (dispatch,) => {
    localStorage.removeItem('userInfo');
    localStorage.removeItem('cartItems');
    localStorage.removeItem('shippingAddress');
    dispatch({
        type: LIST_CONS.USER_SIGNOUT,
    })
}


export const register = (name, email, password) => async (dispatch) => {
    dispatch({
        type: LIST_CONS.USER_REGISTER_REQUEST,
        payload: { name, email, password }
    })
    try {
        const { data } = await Axios.post('/api/users/register', { name, email, password });
        dispatch({
            type: LIST_CONS.USER_REGISTER_SUCCESS,
            payload: data,
        })
        dispatch({
            type: LIST_CONS.USER_SIGNIN_SUCCESS,
            payload: data,
        })
        localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
        dispatch({
            type: LIST_CONS.USER_REGISTER_FAIL,
            payload: error.response && error.response.data.message ? error.response.data.message : error.message
        })
    }
}