import React from 'react';
import { Link } from 'react-router-dom';

function ProductDetail(props) {
    const { product, onAddToCardClick } = props;

    function handleClick() {
        if (onAddToCardClick) {
            const qty = document.querySelector("#num-order").value;
            onAddToCardClick(qty);
        }
    }

    return (
        <div id="main-content-wp" className="detail-product-page clearfix">
            <div className="wp-inner clearfix">
                <div id="sidebar" className="fl-left">
                    <div id="main-menu-wp">
                        <ul className="list-item">
                            <li className="active">
                                <Link to="/" title="Trang chủ">Trang chủ</Link>
                            </li>
                            <li>
                                <Link to="/0" title="Giới thiệu">Giới thiệu</Link>
                            </li>
                            <li>
                                <Link to="?page=category_product" title="">Laptop</Link>
                            </li>
                            <li>
                                <Link to="?page=category_product" title="">Điện thoại</Link>
                            </li>
                            <li>
                                <Link to="?page=category_product" title="">Máy tính bảng</Link>
                            </li>
                            <li>
                                <Link to="?page=detail_news" title="Liên hệ">Liên hệ</Link>
                            </li>
                        </ul>
                    </div>
                </div>

                <div id="content" className="fl-right">

                    <div className="section" id="info-product-wp">
                        <div className="section-detail clearfix">
                            <div className="thumb fl-left">
                                <img src={product.image} alt="" />
                            </div>
                            <div className="detail fl-right">
                                <h3 className="title">{product.name}</h3>
                                <p className="price">{product.price}đ</p>
                                <p className="product-code">Mã sản phẩm: <span>VIETSOZ#123</span></p>
                                <div className="desc-short">
                                    <h5>Mô tả sản phẩm:</h5>
                                    <p>{product.description}</p>
                                </div>
                                <div className="num-order-wp">
                                    <span>Số lượng:</span>
                                    <input id="num-order" name="num-order" defaultValue="1" />
                                    <button className="add-to-cart" onClick={handleClick}>Thêm giỏ hàng</button>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className="section" id="desc-wp">
                        <div className="section-head">
                            <h3 className="section-title">Chi tiết sản phẩm</h3>
                        </div>
                        <div className="section-detail">
                            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
                            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ProductDetail;