import React from 'react';
import CartItem from './CartItem';
import { Link } from 'react-router-dom';
import MessageBox from '../MessageBox/MessageBox';

function Cart(props) {
    const { cartItems, deleteCartItemHandler, changeFromCartHandler, checkoutFromCartHandler } = props;

    function checkoutHandler() {
        checkoutFromCartHandler();
    }

    return (
        <div id="main-content-wp" className="cart-page">
            <div className="section" id="breadcrumb-wp">
                <div className="wp-inner">
                    <div className="section-detail">
                        <h3 className="title">Giỏ hàng</h3>
                    </div>
                </div>
            </div>
            {cartItems.length === 0
                ? <MessageBox>Giỏ hàng không có sản phẩm. <Link to="/">Về trang chủ</Link></MessageBox>
                : (<div id="wrapper" className="wp-inner clearfix">
                    <div className="section" id="info-cart-wp">
                        <div className="section-detail table-responsive">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <td>Mã sản phẩm</td>
                                        <td>Ảnh sản phẩm</td>
                                        <td>Tên sản phẩm</td>
                                        <td>Giá sản phẩm</td>
                                        <td>Số lượng</td>
                                        <td colSpan={2}>Thành tiền</td>
                                    </tr>
                                </thead>
                                <tbody>
                                    {cartItems.map(item => <CartItem key={item.product} item={item}
                                        deleteCartItemHandler={deleteCartItemHandler}
                                        changeFromCartHandler={changeFromCartHandler}
                                    />)}
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colSpan={7}>
                                            <div className="clearfix">
                                                <p id="total-price" className="fl-right">Giỏ hàng có: {cartItems.reduce((a, b) => a + b.qty, 0)} sản phẩm.
                                                Tổng giá: <span>{cartItems.reduce((a, b) => a + b.price * b.qty, 0)}đ</span></p>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan={7}>
                                            <div className="clearfix">
                                                <div className="fl-right">
                                                    <button type="button" id="checkout-cart"
                                                        onClick={checkoutHandler}>Thanh toán</button>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                    </div>
                    <div className="section" id="action-cart-wp">
                        <div className="section-detail">
                            <p className="title"><span>Nhấn vào thanh toán để hoàn tất mua hàng.</span></p>
                            <Link to="/" id="buy-more">Mua tiếp</Link>
                            <br />
                            <a href="true" id="delete-cart">Xóa giỏ hàng</a>
                        </div>
                    </div>
                </div>
                )}
        </div>
    );
}

export default Cart;