import React from 'react';
import { Link } from 'react-router-dom';
import MessageBox from '../MessageBox/MessageBox';
import './SignIn.css';

function Signin(props) {
    const { submitHandlerFromSignInPage, onChangeEmailHandler, onChangePasswordHandler, error, redirect } = props;

    function submitHandler(e) {
        e.preventDefault();
        submitHandlerFromSignInPage();
    }

    return (
        <div>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>Chào mừng đến với <span className="gradient-text">UNITOP</span></h1>
                </div>
                <div>
                    <label htmlFor="email">Email</label>
                    <input type="email" id="email" placeholder="Nhập email"
                        required
                        onChange={(e) => { onChangeEmailHandler(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="password">Mật khẩu</label>
                    <input type="password" id="password" placeholder="Nhập mật khẩu"
                        required
                        onChange={(e) => { onChangePasswordHandler(e.target.value) }}>
                    </input>
                </div>
                {error && <MessageBox>{error}</MessageBox>}
                <div>
                    <button type="submit" className="btn--primary"> Đăng nhập </button>
                </div>
                <div>
                    Bạn chưa có tài khoản?<Link to={`/register?redirect=${redirect}`}>Nhấp vào đây để tạo tài khoản ngay.</Link>
                </div>
            </form>
        </div>
    )
}

export default Signin;