import React, { useEffect } from 'react';
import { addToCart, deleteCartItem } from '../actions/cart';
import { useDispatch, useSelector } from 'react-redux'
import Cart from '../components/Cart/Cart';

function CartPage(props) {
    const productID = props.match.params.id;
    const qty = props.location.search ? Number(props.location.search.split('=')[1]) : 1;
    const dispatch = useDispatch();
    const cart = useSelector(state => state.cart);
    const { cartItems } = cart;

    useEffect(() => {
        if (productID) {
            dispatch(addToCart(productID, qty));
        }
    }, [dispatch, productID, qty]);

    const deleteCartItemHandler = (id) => {
        dispatch(deleteCartItem(id));
    }

    const changeFromCartHandler = (id, qty) => {
        dispatch(addToCart(id, qty));
    }

    const checkoutFromCartHandler = () => {
        props.history.push('/signin?redirect=shipping');
    }

    return (
        <Cart cartItems={cartItems}
            deleteCartItemHandler={deleteCartItemHandler}
            changeFromCartHandler={changeFromCartHandler}
            checkoutFromCartHandler={checkoutFromCartHandler} />
    );
}

export default CartPage;