import * as LIST_CONSTANTS from '../constants/products';
import axios from 'axios';

export const getListProducts = () => async (dispatch) => {
    dispatch({
        type: LIST_CONSTANTS.PRODUCT_LIST_REQUEST,
    });
    try {
        const { data } = await axios.get('/api/products');
        dispatch({
            type: LIST_CONSTANTS.PRODUCT_LIST_SUCCESS,
            payload: data
        });
    } catch (error) {
        dispatch({
            type: LIST_CONSTANTS.PRODUCT_LIST_FAIL,
            payload: error.message
        });
    }
}

export const getDetailProduct = (productID) => async (dispatch) => {
    dispatch({
        type: LIST_CONSTANTS.PRODUCT_DETAIL_REQUEST,
        payload: productID
    });

    try {
        const { data } = await axios.get(`/api/products/${productID}`);
        dispatch({
            type: LIST_CONSTANTS.PRODUCT_DETAIL_SUCCESS,
            payload: data
        });
    } catch (error) {
        dispatch({
            type: LIST_CONSTANTS.PRODUCT_DETAIL_FAIL,
            payload: error.response && error.response.data.message ? error.response.data.message : error.message
        });
    }
}
