import React from 'react';
import { Link } from 'react-router-dom';
import './Cart.css';

function CartItem(props) {
    const { item, deleteCartItemHandler, changeFromCartHandler } = props;
    return (
        <tr>
            <td>{item.product}</td>
            <td>
                <Link to={`/products/${item.product}`} className="thumb">
                    <img src={item.image} alt={item.name} />
                </Link>
            </td>
            <td>
                <a href="true" className="name-product">{item.name}</a>
            </td>
            <td>{item.price}đ</td>
            <td>
                <input type="text" name="num-order" className="num-order" defaultValue={item.qty}
                    onChange={(e) => changeFromCartHandler(item.product, Number(e.target.value))} />
            </td>
            <td>
                <button type="button" className="del-product" onClick={() => deleteCartItemHandler(item.product)}><i className="fa fa-trash-o" /></button>
            </td>
        </tr>
    );
}

export default CartItem;