import * as LIST_CONSTANTS from '../constants/products';

export const productsListReducer = (state = { loading: true, product: [] }, action) => {
    switch (action.type) {
        case LIST_CONSTANTS.PRODUCT_LIST_REQUEST:
            return { loading: true };
        case LIST_CONSTANTS.PRODUCT_LIST_SUCCESS:
            return {
                loading: false,
                products: action.payload
            }
        case LIST_CONSTANTS.PRODUCT_LIST_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default:
            return state;
    }
};

export const productDetailReducer = (state = { loading: true, product: {} }, action) => {
    switch (action.type) {
        case LIST_CONSTANTS.PRODUCT_DETAIL_REQUEST:
            return { loading: true };
        case LIST_CONSTANTS.PRODUCT_DETAIL_SUCCESS:
            return {
                loading: false,
                product: action.payload
            }
        case LIST_CONSTANTS.PRODUCT_DETAIL_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default:
            return state;
    }
};

