import React from 'react';
import Product from './Product';

function Products(props) {
    const { products } = props;
    return (
        <div id="main-content-wp" className="home-page">
            <div className="wp-inner clearfix">
                <div id="sidebar" className="fl-left">
                    <div id="main-menu-wp">
                        <ul className="list-item">
                            <li className="active">
                                <a href="?page=home" title="Trang chủ">Trang chủ</a>
                            </li>
                            <li>
                                <a href="?page=detail_news" title="Giới thiệu">Giới thiệu</a>
                            </li>
                            <li>
                                <a href="?page=category_product" title="">Laptop</a>
                            </li>
                            <li>
                                <a href="?page=category_product" title="">Điện thoại</a>
                            </li>
                            <li>
                                <a href="?page=category_product" title="">Máy tính bảng</a>
                            </li>
                            <li>
                                <a href="?page=detail_news" title="Liên hệ">Liên hệ</a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div id="content" className="fl-right">
                    <div className="section list-cat">
                        <div className="section-head">
                            <h3 className="section-title">Laptop</h3>
                        </div>
                        <div className="section-detail">
                            <ul className="list-item clearfix">
                                {
                                    products.map((product) =>
                                        product.type === 'Laptop' ? (<Product key={product._id} product={product} />) : ''
                                    )
                                }
                            </ul>
                        </div>
                    </div>
                    <div className="section list-cat">
                        <div className="section-head">
                            <h3 className="section-title">Điện thoại</h3>
                        </div>
                        <div className="section-detail">
                            <ul className="list-item clearfix">
                                {
                                    products.map((product) =>
                                        product.type === 'Phone' ? (<Product key={product._id} product={product} />) : ''
                                    )
                                }
                            </ul>
                        </div>
                    </div>
                    <div className="section list-cat">
                        <div className="section-head">
                            <h3 className="section-title">Máy tính bảng</h3>
                        </div>
                        <div className="section-detail">
                            <ul className="list-item clearfix">
                                {
                                    products.map((product) =>
                                        product.type === 'Tablet' ? (<Product key={product._id} product={product} />) : ''
                                    )
                                }
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Products;