import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CheckoutSteps from '../components/CheckoutSteps/CheckoutSteps';
import { Link } from 'react-router-dom';
import { createOrder } from '../actions/order';
import { ORDER_CREATE_RESET } from '../constants/order';
import LoadingBox from '../components/LoadingBox/LoadingBox';
import MessageBox from '../components/MessageBox/MessageBox';

function PlaceOrderPage(props) {
    const cart = useSelector(state => state.cart);
    if (!cart.paymentMethod) {
        props.history.push('/payment');
    }

    const orderCreate = useSelector(state => state.orderCreate);
    const { loadding, success, error, order } = orderCreate;
    cart.itemsPrice = cart.cartItems.reduce((a, b) => a + b.price * b.qty, 0);
    cart.shippingPrice = cart.itemsPrice > 5000000 ? 0 : 20000
    cart.totalPrice = cart.itemsPrice + cart.shippingPrice;

    const dispatch = useDispatch();
    const placeOrderhandler = () => {
        dispatch(createOrder({ ...cart, orderItems: cart.cartItems }));
    }

    useEffect(() => {
        if (success) {
            props.history.push(`/orders/${order._id}`);
            dispatch({ type: ORDER_CREATE_RESET })
        }
    }, [dispatch, order, props.history, success])

    return (
        <div>
            <CheckoutSteps step1 step2 step3 step4></CheckoutSteps>
            <div className="row top">
                <div className="col-2">
                    <ul>
                        <li>
                            <div className="card card-body">
                                <h2>Thông tin của bạn</h2>
                                <p>
                                    <strong>Họ và tên:</strong> {cart.shippingAddress.fullName} <br />
                                    <strong>Địa chỉ:</strong> {cart.shippingAddress.address} <br />
                                    <strong>Số điện thoại:</strong> {cart.shippingAddress.phoneNumber}
                                </p>
                            </div>
                        </li>
                        <li>
                            <div className="card card-body">
                                <h2>Phương thức thanh toán </h2>
                                <p>
                                    <strong>Tên phương thức: </strong> {cart.paymentMethod}
                                </p>
                            </div>
                        </li>
                        <li>
                            <div className="card card-body " id="info-cart-wp">
                                <h2>Danh sách sản phẩm </h2>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <td>Mã sản phẩm</td>
                                            <td>Ảnh sản phẩm</td>
                                            <td>Tên sản phẩm</td>
                                            <td>Giá sản phẩm</td>
                                            <td>Số lượng</td>
                                            <td colSpan={2}>Thành tiền</td>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {cart.cartItems.map(item => (<tr key={item.product}>
                                            <td>{item.product}</td>
                                            <td>
                                                <Link to={`/products/${item.product}`} className="thumb">
                                                    <img src={item.image} alt={item.name} />
                                                </Link>
                                            </td>
                                            <td>
                                                <a href="true" className="name-product">{item.name}</a>
                                            </td>
                                            <td>{item.price}đ</td>
                                            <td> {item.qty}</td>
                                            <td>{item.price * item.qty}đ</td>

                                        </tr>))}
                                    </tbody>
                                </table>
                            </div>
                        </li>
                    </ul>
                </div>
                <div className="col-1">
                    <div className="card card-body">
                        <ul>
                            <li><h2>Tổng quát</h2></li>
                            <li>
                                <div className="row">
                                    <div className="mr-5">Tổng tiền các sản phẩm: </div>
                                    <div> {cart.itemsPrice}đ</div>
                                </div>

                            </li>
                            <li>
                                <div className="row">
                                    <div className="mr-5">Phí giao hàng: </div>
                                    <div> {cart.shippingPrice}đ</div>
                                </div>
                            </li>
                            <li>
                                <div className="row">
                                    <div className="mr-5"><strong>Tổng tiền phải trả:</strong> </div>
                                    <div><strong>{cart.totalPrice}đ</strong> </div>
                                </div>
                            </li>
                            <li>
                                <button type="button" onClick={placeOrderhandler} className="btn--primary place-order-btn">Đặt hàng</button>
                            </li>
                            {loadding && <LoadingBox></LoadingBox>}
                            {error && <MessageBox>{error}</MessageBox>}
                        </ul>

                    </div>
                </div>
            </div>
        </div>
    );
}

export default PlaceOrderPage;