import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CheckoutSteps from '../components/CheckoutSteps/CheckoutSteps';
import { saveShippingAddress } from '../actions/cart';

function ShippingAddressPage(props) {
    const userSignin = useSelector(state => state.userSignin);
    const { userInfo } = userSignin;

    const cart = useSelector(state => state.cart);
    const { shippingAddress } = cart;

    if (!userInfo) {
        props.history.push('/signin');
    }

    const [fullName, setFullName] = useState(shippingAddress.fullName);
    const [address, setAddress] = useState(shippingAddress.address);
    const [phoneNumber, setPhoneNumber] = useState(shippingAddress.phoneNumber);

    const dispatch = useDispatch();

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(saveShippingAddress({ fullName, address, phoneNumber }));
        props.history.push('/payment');
    }

    return (
        <div>
            <CheckoutSteps step1 step2></CheckoutSteps>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>Thông tin người nhận hàng</h1>
                </div>
                <div>
                    <label htmlFor="fullname">Họ và tên</label>
                    <input type="text" id="fullname" placeholder="Nhập họ và tên"
                        required
                        value={fullName}
                        onChange={(e) => { setFullName(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="address">Địa chỉ</label>
                    <input type="text" id="address" placeholder="Nhập địa chỉ"
                        required
                        value={address}
                        onChange={(e) => { setAddress(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="phoneNumber">Số điện thoại</label>
                    <input type="text" id="phoneNumber" placeholder="Nhập số điện thoại"
                        required
                        value={phoneNumber}
                        onChange={(e) => { setPhoneNumber(e.target.value) }}>
                    </input>
                </div>

                <div>
                    <button type="submit" className="btn--primary"> Tiến hành thanh toán </button>
                </div>
            </form>
        </div>
    );
}

export default ShippingAddressPage;