import React from 'react';
import { Link } from 'react-router-dom';
import MessageBox from '../MessageBox/MessageBox';

function Register(props) {
    const { submitHandlerFromRegisterPage, onChangeNameHandler,
        onChangeEmailHandler, onChangePasswordHandler,
        onChangeConfirmPasswordHandler, error, redirect } = props;

    function submitHandler(e) {
        e.preventDefault();
        submitHandlerFromRegisterPage();
    }

    return (
        <div>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>Chào mừng đến với <span className="gradient-text">UNITOP</span></h1>
                </div>
                <div>
                    <label htmlFor="name">Name</label>
                    <input type="text" id="name" placeholder="Nhập tên"
                        required
                        onChange={(e) => { onChangeNameHandler(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="email">Email</label>
                    <input type="email" id="email" placeholder="Nhập email"
                        required
                        onChange={(e) => { onChangeEmailHandler(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="password">Mật khẩu</label>
                    <input type="password" id="password" placeholder="Nhập mật khẩu"
                        required
                        onChange={(e) => { onChangePasswordHandler(e.target.value) }}>
                    </input>
                </div>
                <div>
                    <label htmlFor="re-password">Xác nhận mật khẩu</label>
                    <input type="password" id="re-password" placeholder="Nhập lại mật khẩu"
                        required
                        onChange={(e) => { onChangeConfirmPasswordHandler(e.target.value) }}>
                    </input>
                </div>
                {error && <MessageBox>{error}</MessageBox>}
                <div>
                    <button type="submit" className="btn--primary"> Đăng kí </button>
                </div>
                <div>
                    Bạn đã có tài khoản?<Link to={`/signin?redirect=${redirect}`}>Nhấp vào đây để đăng nhập ngay.</Link>
                </div>
            </form>
        </div>
    )
}

export default Register;