import React, { useEffect, useState } from 'react';
import Register from '../components/Register/Register';
import { useDispatch, useSelector } from 'react-redux';
import { register } from '../actions/user';
import LoadingBox from '../components/LoadingBox/LoadingBox';

function RegisterPage(props) {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');

    const redirect = props.location.search ? props.location.search.split('=')[1] : '/';

    const userRegister = useSelector(state => state.userRegister);
    const { userInfo, loading, error } = userRegister;

    const dispatch = useDispatch();

    const submitHandlerFromRegisterPage = () => {
        if (password !== confirmPassword) {
            alert('Mật khẩu và mật khẩu xác nhận không trùng khớp')
        } else {
            dispatch(register(name, email, password));
        }
    }

    useEffect(() => {
        if (userInfo) {
            props.history.push(redirect);
        }
    }, [props.history, redirect, userInfo])

    const onChangeNameHandler = (value) => setName(value);

    const onChangeEmailHandler = (value) => setEmail(value);

    const onChangePasswordHandler = (value) => setPassword(value);

    const onChangeConfirmPasswordHandler = (value) => setConfirmPassword(value);

    return (
        <>
            { loading && <LoadingBox></LoadingBox>}

            <Register submitHandlerFromRegisterPage={submitHandlerFromRegisterPage}
                onChangeNameHandler={onChangeNameHandler}
                onChangeEmailHandler={onChangeEmailHandler}
                onChangePasswordHandler={onChangePasswordHandler}
                onChangeConfirmPasswordHandler={onChangeConfirmPasswordHandler}
                error={error}
                redirect={redirect}
            />
        </>)
}

export default RegisterPage;