import axios from 'axios';
import * as LIST_CONSTANT from '../constants/cart';

export const addToCart = (productID, qty) => async (dispatch, getState) => {
    try {
        const { data } = await axios.get(`/api/products/${productID}`);
        dispatch({
            type: LIST_CONSTANT.CART_ADD_ITEM,
            payload: {
                product: data._id,
                image: data.image,
                name: data.name,
                price: data.price,
                qty,
            }
        })
        localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems));
    } catch (error) {
        console.log(error.message);
    }
}

export const deleteCartItem = (productID) => async (dispatch, getState) => {
    dispatch({
        type: LIST_CONSTANT.CART_DELETE_ITEM,
        payload: productID
    })
    localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems));
}

export const saveShippingAddress = (data) => (dispatch) => {
    dispatch({
        type: LIST_CONSTANT.CART_SAVE_SHIPPING_ADDRESS,
        payload: data
    })
    localStorage.setItem('shippingAddress', JSON.stringify(data));
}

export const savePaymentMethod = (data) => (dispatch) => {
    dispatch({
        type: LIST_CONSTANT.CART_SAVE_PAYMENT_METHOD,
        payload: data
    })
}