import React from 'react';
import { Link } from 'react-router-dom';

function Header(props) {
    const { cartNum, userInfo, signOutHandler } = props;

    const handleSignOut = () => {
        signOutHandler();
    }

    return (
        <div id="site">
            <div id="container">
                <div id="header-wp" className="clearfix">
                    <div className="wp-inner">
                        <Link to="/" id="logo" className="fl-left">UNITOP STORE</Link>
                        <div id="btn-respon" className="fl-right"><i className="fa fa-bars" aria-hidden="true" /></div>
                        {
                            userInfo ? (
                                <div id="btn-signin" className="fl-right">
                                    <Link to="#" > {userInfo.name} <i className="fa fa-caret-down"></i></Link>
                                    <ul className="user-dropdown" >
                                        <li>
                                            <Link to="#signout" onClick={handleSignOut}>Sign Out</Link>
                                        </li>
                                    </ul>
                                </div>
                            ) :
                                (<div id="btn-signin" className="fl-right"> <Link to="/signin">Đăng nhập</Link></div>)
                        }

                        <div id="cart-wp" className="fl-right ">
                            <Link to="/cart" id="btn-cart">
                                <span id="icon"><img src="/images/icon-cart.png" alt="" /></span>
                                <span id="num">{cartNum}</span>
                            </Link>
                        </div>

                    </div>

                </div>
            </div>
        </div>
    );
}

export default Header;