import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { detailsOrder } from '../actions/order';
import LoadingBox from '../components/LoadingBox/LoadingBox';
import MessageBox from '../components/MessageBox/MessageBox';
import { PayPalButton } from 'react-paypal-button-v2';

function OrderPage(props) {
    const orderId = props.match.params.id;
    const [sdkReady, setSdkReady] = useState(false);
    const orderDetail = useSelector(state => state.orderDetail);
    const { loading, error, order } = orderDetail;
    const dispatch = useDispatch();

    useEffect(() => {
        const addPaypalScript = async () => {
            const { data } = await Axios.get('/api/config/paypal');
            const script = document.createElement('script');
            script.type = 'text/javascript';
            script.src = `https://www.paypal.com/sdk/js?client-id=${data}`;
            script.async = true;
            script.onload = () => {
                setSdkReady(true);
            };
            document.body.appendChild(script);
        }

        if (!order) {
            dispatch(detailsOrder(orderId));
        } else {
            if (!order.isPaid) {
                if (!window.paypal) {
                    addPaypalScript();
                } else {
                    setSdkReady(true);
                }
            }
        }

    }, [dispatch, order, orderId, sdkReady]);

    const successPaymenthandler = () => {

    }

    return (
        loading ? (<LoadingBox></LoadingBox>) :
            error ? (<MessageBox>{error}</MessageBox>) : (
                <div>
                    <h1 className="order-id">Mã đơn hàng: {order._id} </h1>
                    <div className="row top">
                        <div className="col-2">
                            <ul>
                                <li>
                                    <div className="card card-body">
                                        <h2>Thông tin của bạn</h2>
                                        <p>
                                            <strong>Họ và tên:</strong> {order.shippingAddress.fullName} <br />
                                            <strong>Địa chỉ:</strong> {order.shippingAddress.address} <br />
                                            <strong>Số điện thoại:</strong> {order.shippingAddress.phoneNumber}
                                        </p>
                                        {order.isDelivered ? (<MessageBox check="isDelivered"> Đã giao hàng lúc {order.deliveredAt}</MessageBox>)
                                            : (<MessageBox check="isDelivered">Chưa giao hàng</MessageBox>)}
                                    </div>
                                </li>
                                <li>
                                    <div className="card card-body">
                                        <h2>Phương thức thanh toán </h2>
                                        <p>
                                            <strong>Tên phương thức: </strong> {order.paymentMethod}
                                        </p>
                                        {order.isPaid ? (<MessageBox check="isDelivered"> Đã thanh toán {order.deliveredAt}</MessageBox>)
                                            : (<MessageBox check="isDelivered">Chưa thanh toán</MessageBox>)}
                                    </div>
                                </li>
                                <li>
                                    <div className="card card-body " id="info-cart-wp">
                                        <h2>Danh sách sản phẩm </h2>
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <td>Mã sản phẩm</td>
                                                    <td>Ảnh sản phẩm</td>
                                                    <td>Tên sản phẩm</td>
                                                    <td>Giá sản phẩm</td>
                                                    <td>Số lượng</td>
                                                    <td colSpan={2}>Thành tiền</td>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {order.orderItems.map(item => (<tr key={item.product}>
                                                    <td>{item.product}</td>
                                                    <td>
                                                        <Link to={`/products/${item.product}`} className="thumb">
                                                            <img src={item.image} alt={item.name} />
                                                        </Link>
                                                    </td>
                                                    <td>
                                                        <a href="true" className="name-product">{item.name}</a>
                                                    </td>
                                                    <td>{item.price}đ</td>
                                                    <td> {item.qty}</td>
                                                    <td>{item.price * item.qty}đ</td>

                                                </tr>))}
                                            </tbody>
                                        </table>
                                    </div>
                                </li>
                            </ul>
                        </div>
                        <div className="col-1">
                            <div className="card card-body">
                                <ul>
                                    <li><h2>Tổng quát</h2></li>
                                    <li>
                                        <div className="row">
                                            <div className="mr-5">Tổng tiền các sản phẩm: </div>
                                            <div> {order.itemsPrice}đ</div>
                                        </div>

                                    </li>
                                    <li>
                                        <div className="row">
                                            <div className="mr-5">Phí giao hàng: </div>
                                            <div> {order.shippingPrice}đ</div>
                                        </div>
                                    </li>
                                    <li>
                                        <div className="row">
                                            <div className="mr-5"><strong>Tổng tiền phải trả:</strong> </div>
                                            <div><strong>{order.totalPrice}đ</strong> </div>
                                        </div>
                                    </li>
                                    {
                                        !order.isPaid && (
                                            <li>
                                                {!sdkReady ? (<LoadingBox></LoadingBox>)
                                                    : (
                                                        <PayPalButton amount={order.totalPrice} onSuccess={successPaymenthandler}></PayPalButton>
                                                    )
                                                }
                                            </li>
                                        )
                                    }
                                </ul>

                            </div>
                        </div>
                    </div>
                </div>)

    );
}

export default OrderPage;