import * as LIST_ORDER from '../constants/order';
import { CART_EMPTY } from '../constants/cart';
import Axios from 'axios';

export const createOrder = (order) => async (dispatch, getState) => {
    dispatch({
        type: LIST_ORDER.ORDER_CREATE_REQUEST,
        payload: order
    })
    try {
        const { userSignin: { userInfo } } = getState();
        const { data } = await Axios.post('/api/orders', order, {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            },
        })
        dispatch({
            type: LIST_ORDER.ORDER_CREATE_SUCCESS,
            payload: data.order
        })
        dispatch({
            type: CART_EMPTY
        })
        localStorage.removeItem('cartItems');
    } catch (error) {
        dispatch({
            type: LIST_ORDER.ORDER_CREATE_FAIL,
            payload: error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        })
    }

}

export const detailsOrder = (orderId) => async (dispatch, getState) => {
    dispatch({
        type: LIST_ORDER.ORDER_DETAIL_REQUEST,
        payload: orderId
    })
    const { userSignin: { userInfo } } = getState();
    try {
        const { data } = await Axios.get(`/api/orders/${orderId}`, {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            }
        });
        dispatch({
            type: LIST_ORDER.ORDER_DETAIL_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: LIST_ORDER.ORDER_DETAIL_FAIL,
            payload: error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        })
    }
}